.cart__item {
   transition: 0.2s all ease-in-out;
}
.cart__item:hover {
   transform: translateX(-3px);
   box-shadow: 1px 20px 30px -40px rgb(155, 155, 155);
}
.item-cart {
   display: flex;
   align-items: center;
   padding: 30px 0;
   font-size: 22px;
   font-weight: 700;
   display: grid;
   grid-template-columns: 5fr 1fr 2fr 1fr 1.5fr 1fr 1fr;
}
@media (max-width: 614px) {
   .item-cart {
      font-size: 19px;
   }
}
@media (max-width: 360px) {
   .item-cart {
      grid-template-columns: 5fr 1fr 2fr 0.5fr 1.5fr 0.5fr 1fr;
   }
}
.item-cart:not(:last-child) {
   border-bottom: 1px solid #f4f4f4;
}
.items-cart__about {
   display: flex;
   align-items: center;
   column-gap: 15px;
}
@media (max-width: 455px) {
   .items-cart__about {
      flex-direction: column;
      align-items: flex-start;
      row-gap: 5px;
   }
}
.item-cart__img {
   width: 80px;
}
@media (max-width: 614px) {
   .item-cart__img {
      width: 50px;
   }
}
@media (max-width: 455px) {
   .item-cart__img {
      width: 70px;
   }
}

.item-cart__text-block {
   display: flex;
   flex-direction: column;
   row-gap: 3px;
}
.item-cart__title {
}
.item-cart__subtitle {
   font-size: 18px;
   color: #8d8d8d;
   font-weight: 400;
}
@media (max-width: 614px) {
   .item-cart__subtitle {
      font-size: 16px;
   }
}
.item-cart__counter {
   display: flex;
   align-items: center;
   grid-column-start: 3;
   column-gap: 12px;
}
@media (max-width: 614px) {
   .item-cart__counter {
      flex-direction: column;
      row-gap: 5px;
   }
}

.item-cart__btn {
   width: 32px;
   height: 32px;
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 50%;
   transition: 0.4s all ease-in-out;
}
@media (max-width: 360px) {
   .item-cart__btn {
      width: 28px;
      height: 28px;
   }
}
.item-cart__btn.btn-count:active {
   background-color: #b63a09;
}
.item-cart__btn.disabled {
   border: 2px solid #dbdbdb;
}
.item-cart__btn.disabled svg path {
   fill: #dbdbdb;
}
.item-cart__btn.btn-count:not(.btn-close):hover {
   background-color: #fe5f1e;
}
.item-cart__btn.btn-count:not(.btn-close):hover svg path {
   fill: white;
}
.item-cart__btn.btn-count {
   border: 2px solid #fe5f1e;
}
.item-cart__count {
}

.item-cart__price {
   grid-column-start: 5;
   white-space: nowrap;
}

.item-cart__close {
   width: 32px;
   height: 32px;

   border-radius: 50%;
}
.item-cart__btn.btn-close {
   border: 2px solid #e9e9e9;
   grid-column-start: 7;
   justify-self: flex-end;
   transition: 0.9s all ease-in-out;
}
.btn-close:hover {
   border: 2px solid #dbdbdb;
}
.btn-close:hover {
   transform: rotate(-45deg);
}
.btn-close:hover svg path {
   fill: #dbdbdb;
}
