.cart {
   padding: 94px 0 131px 0;
}
@media (max-width: 890px) {
   .cart {
      padding: 40px 0;
   }
}
.cart__container {
   max-width: 821px;
}
.container {
}
.cart__title-block {
   padding-bottom: 30px;
   border-bottom: 1px solid #f4f4f4;
}
.tilte-block-cart {
   display: flex;
   align-items: center;
   justify-content: space-between;
}
@media (max-width: 454px) {
   .tilte-block-cart {
      flex-direction: column;
      align-items: flex-start;
      row-gap: 10px;
      /*align-items: flex-start;*/
   }
}
.tilte-block-cart__left {
   display: flex;
   align-items: center;
   column-gap: 17px;
}
.tilte-block-cart__cart-icon {
}
.tilte-block-cart__title {
   font-size: 32px;
   font-weight: 700;
}
.tilte-block-cart__right {
   display: flex;
   column-gap: 7px;
}
.tilte-block-cart__trash-icon {
}
a.tilte-block-cart__link {
   font-size: 16px;
   color: #acacac;
}
.final-cart

/*----------------------------------------------cart product----------------------------------------------------------*/

.cart__items {
   margin-bottom: 40px;
}

.cart__final {
   display: flex;
   font-size: 22px;
   justify-content: space-between;
   margin-bottom: 40px;
   border-top: 2px solid #808080;
   padding-top: 40px;
}
@media (max-width: 430px) {
   .cart__final {
      font-size: 20px;
   }
}
.final-cart {
}
.final-cart__left {
   display: flex;
   column-gap: 10px;
}
@media (max-width: 570px) {
   .final-cart__left {
      flex-direction: column;
   }
}
.final-cart__text {
}
.final-cart__number {
   font-weight: 700;
}
.final-cart__right {
   display: flex;
   column-gap: 10px;
}
@media (max-width: 570px) {
   .final-cart__right {
      flex-direction: column;
   }
}
.final-cart__price {
   font-weight: 700;
   color: #fe5f1e;
}

.cart__btn-block {
   display: flex;
   justify-content: space-between;
   font-weight: 700;
   font-size: 16px;
}
@media (max-width: 573px) {
   .cart__btn-block {
      flex-direction: column;
      /*align-items: flex-start;*/
      row-gap: 10px;
   }
}
.cart__return {
   color: #cacaca;
   border: 1px solid #d3d3d3;
   transition: 0.2s all ease-in-out;
}
.cart__return:hover {
   border: 1px solid #b4b4b4;
   color: #b4b4b4;
}
.cart__return:active {
   border: 1px solid #8b8b8b;
   color: #8b8b8b;
}
.cart__purchaise {
   background-color: #fe5f1e;
   color: white;
   transition: 0.2s all ease-in-out;
}
.cart__purchaise:hover {
   background-color: #f74600;
}
.cart__purchaise:active {
   background-color: #ec4300;
}
.cart-btn {
   padding: 18px 40px;
   border-radius: 30px;
   display: flex;
   align-items: center;
   column-gap: 10px;
}
